import {submitWXOrder} from '@/http/product';

function formatTime(time) {
	if (typeof time !== 'number' || time < 0) {
		return time
	}

	var hour = parseInt(time / 3600)
	time = time % 3600
	var minute = parseInt(time / 60)
	time = time % 60
	var second = time

	return ([hour, minute, second]).map(function (n) {
		n = n.toString()
		return n[1] ? n : '0' + n
	}).join(':')
}

function formatLocation(longitude, latitude) {
	if (typeof longitude === 'string' && typeof latitude === 'string') {
		longitude = parseFloat(longitude)
		latitude = parseFloat(latitude)
	}

	longitude = longitude.toFixed(2)
	latitude = latitude.toFixed(2)

	return {
		longitude: longitude.toString().split('.'),
		latitude: latitude.toString().split('.')
	}
}
var dateUtils = {
	UNITS: {
		'': 31557600000,
		'': 2629800000,
		'': 86400000,
		'': 3600000,
		'': 60000,
		'': 1000
	},
	humanize: function (milliseconds) {
		var humanize = '';
		for (var key in this.UNITS) {
			if (milliseconds >= this.UNITS[key]) {
				humanize = Math.floor(milliseconds / this.UNITS[key]) + key + '';
				break;
			}
		}
		return humanize || '';
	},
	format: function (dateStr) {
		var date = this.parse(dateStr)
		var diff = Date.now() - date.getTime();
		if (diff < this.UNITS['']) {
			return this.humanize(diff);
		}
		var _format = function (number) {
			return (number < 10 ? ('0' + number) : number);
		};
		return date.getFullYear() + '/' + _format(date.getMonth() + 1) + '/' + _format(date.getDay()) + '-' +
			_format(date.getHours()) + ':' + _format(date.getMinutes());
	},
	parse: function (str) { //"yyyy-mm-dd HH:MM:ss"Date
		var a = str.split(/[^0-9]/);
		return new Date(a[0], a[1] - 1, a[2], a[3], a[4], a[5]);
	},
	getTimeF: function(end,time){
		//type end ,  end
		const obj ={d:0,h:0,m:0,s:0,ifover:true};
		let startTime = 0;
		let endTime =0;
		if(!time){
			startTime = new Date().getTime();
			endTime = new Date(Date.parse(end.replace(/-/g,   "/"))).getTime();
		}else{
			startTime = new Date().getTime();
			endTime = new Date(Date.parse(end.replace(/-/g,   "/"))).getTime()+ time* 60*1000;
			
		}
		
		let btw = endTime -startTime;
		if(btw<=0){
			btw = 0;
		}else{
			obj.ifover = false;
		}
		console.log('btw',btw)
		obj.d = Math.floor(btw/(1000*60*60*24));
		let hTime = btw%(1000*60*60*24);
		
		obj.h = Math.floor(hTime/(1000*60*60));
		let mTime = hTime%(1000*60*60);
		obj.m = Math.floor(mTime/(1000*60));
		let sTime = mTime%(1000*60);
		obj.s = Math.floor(sTime/1000);
		return obj; 
	},
	timeToDate: function(time){
		let timeStr = new Date(Date.parse(time.replace(/-/g,   "/"))).getTime();
		let d = new Date();
		d.setTime(timeStr);
		return d.getFullYear() + '' + (d.getMonth()+1) + '' + d.getDate()+ '';
	},
	timeToDateTime: function(time){
		let timeStr = new Date(Date.parse(time.replace(/-/g,   "/"))).getTime();
		let d = new Date();
		d.setTime(timeStr);
		return d.getFullYear() + '' + (d.getMonth()+1) + '' + d.getDate()+ ' ' + d.getHours()+':'+d.getMinutes();
	}
};
//   if(validator.ifEmpty('','')){return ;}
export const validator={
  ifEmpty:(value,tips)=>{  //
    if(!value){
      uni.showToast({
      	title: tips,
      	duration: 2000,
		icon:'none'
      });
      return true;
    }
    return false;
  },
  ifPhone:(value,tips)=>{
    if(!/^1[1-9][0-9]{9}$/.test(value)){  //
      uni.showToast({
      	title: tips,
      	duration: 2000,
		icon:'none'
      });
      return true;
    }
  },
  ifEmail:(value,tips)=>{
    if(!(/^(\w)+(\.\w+)*@(\w)+((\.\w+)+)$/.test(value))){ //
      uni.showToast({
      	title: tips,
      	duration: 2000,
		icon:'none'
      });
      return true;
    }
  },
  ifCode:(value,tips)=>{
    if(!/^[0-9]{4}$/.test(value)){  //4
      uni.showToast({
      	title: tips,
      	duration: 2000,
		icon:'none'
      });
      return true;
    }
  },
  ifSame:(value1,value2,tips)=>{ //  
    if(value1 != value2){
      uni.showToast({
      	title: tips,
      	duration: 2000,
		icon:'none'
      });
      return true;
    }
  },
  ifIDCard:(value,tips)=>{ // 
    let reg=/^[1-9]{1}[0-9]{14}$|^[1-9]{1}[0-9]{16}([0-9]|[xX])$/;
    if(!reg.test(value)){
	  uni.showToast({
	  	title: tips,
	  	duration: 2000,
		icon:'none'
	  });
      return true;
    }
  },
  ifEnNum:(value, tips)=>{
    let reg = /^[0-9a-zA-Z]*$/;
    if(!reg.test(value)){
      uni.showToast({
      	title: tips,
      	duration: 2000,
		icon:'none'
      });
      return true;
    }
  }
};
function gotoSuccess(price,orderNo,id){
	setTimeout(()=>{
		uni.reLaunch({
			url: '/pages/product/paySuccess?price='+price+'&orderNo='+orderNo+'&id='+id
		});
	},1900)
};
function gotoDetail(id){
	setTimeout(()=>{
		uni.reLaunch({
			url: '/pages/order/detail?id='+id
		});
	},1900)
}
function onBridgeReady(appId,timeStamp,nonceStr,paypackage,paySign,price,orderNo,id){
		
   	WeixinJSBridge.invoke(
      	'getBrandWCPayRequest', {
         	"appId":appId,         //     
			"timeStamp":timeStamp, //1970     
			"nonceStr":nonceStr,   //     
			"package":paypackage,
			"signType":"MD5",      //     
			"paySign":paySign      // 
      },
      function(res){
      	if(res.err_msg == "get_brand_wcpay_request:ok" ){
			uni.showToast({
				title: '',
				duration: 2000,
			});
			gotoSuccess(price,orderNo,id);
		} else if(res.err_msg == "get_brand_wcpay_request:fail"){  
			uni.showToast({
				title: '',
				duration: 2000,
				icon:'none'
			});
			setTimeout(()=>{
				uni.reLaunch({
					url: '/pages/order/detail?id='+id
				});
			},1900)
		}  
	}); 
}
function submitWXOrderFun(id,price,orderNo){
	submitWXOrder({id: id}).then((dataR)=>{
		let data = dataR.data.result;
		if(data.return_code =="FAIL"){
			uni.showToast({
				title: data.return_msg,
				duration: 2000,
					icon:'none'
			});
			gotoDetail(id)
		}else{
			if(data.result_code=="FAIL"){
				uni.showToast({
					title: data.err_code_des,
					duration: 2000,
						icon:'none'
				});
				gotoDetail(id)
			}else{
				if(data.needPay==='FAIL'){
					uni.showToast({
						title: '',
						duration: 2000,
						icon:'none'
					});
					gotoDetail(id)
				}else{
					//window.location.href= data.mwebUrl;
					var payappId = data.payappId;
					var paytimestamp = data.paytimestamp;
					var paynonceStr = data.paynonceStr;
					var paypackage = data.paypackage;
					var paySign = data.paySign;
					//var sendUrl = data.sendUrl;
					onBridgeReady(payappId,paytimestamp,paynonceStr,paypackage,paySign,price,orderNo,id);
				}
				
			}
		}
	}).catch((e)=>{
		uni.showToast({
			title: '',
			duration: 2000,
			icon:'none'
		});
		gotoDetail(id)
	});
}


 
